/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

ul {
  padding: 0;
  list-style: none;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  padding: 5rem 0;
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__content {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .header__content {
    padding: 13rem 0;
  }
}
.header__title {
  color: #FFFFFF;
  line-height: 1.5;
  font-size: 4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__title {
    max-width: 50%;
    font-size: 6rem;
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .header__title {
    max-height: 40%;
  }
}
.header__button {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.header__button:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
    display: inline-block;
  }
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}
.section--benefits {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .section__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
@media (min-width: 768px) {
  .section__grid--benefits {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 768px) {
  .section__grid--testimonials {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.section__img--benefits {
  grid-column: 4/6;
  transition-property: transform;
  transition-duration: 0.3s;
}
.section__img--benefits:hover {
  transform: scale(1.1);
}
.section__img--testimonials {
  grid-column: 1/3;
}
.section__content--benefits {
  grid-column: 1/4;
}
.section__content--testimonials {
  grid-column: 3/6;
}
.section__title {
  display: inline-block;
  margin-top: 5rem;
  font-size: 3.6rem;
}
.section__title::after {
  content: "";
  width: calc(100% + 1rem);
  height: 1rem;
  display: block;
  background-color: #84d9bc;
  margin-top: -1.5rem;
}
@media (min-width: 768px) {
  .section__title {
    margin-top: 0;
  }
}
.section__title--benefits::after {
  background-color: #FFFFFF;
}
.section__title--testimonials {
  color: #313c4e;
}
.section__paragraph {
  font-size: 1.8rem;
  line-height: 1.8;
}
.section__paragraph--testimonials {
  margin: 0;
  padding-left: 6rem;
}
.section__testimonial {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  border-radius: 1.5rem;
  margin-bottom: 4rem;
  padding: 2.5rem;
  position: relative;
  font-size: 1.8rem;
  line-height: 1.8;
}
.section__testimonial::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 5rem;
  height: 5rem;
  display: inline-block;
  position: absolute;
}
@media (min-width: 768px) {
  .section__testimonial {
    max-width: 70%;
  }
  .section__testimonial:nth-of-type(1) {
    margin-left: 10%;
  }
  .section__testimonial:nth-of-type(2) {
    margin-left: 20%;
  }
  .section__testimonial:nth-of-type(3) {
    margin-left: 30%;
  }
}

.subscription {
  background-color: #313c4e;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .subscription {
    padding: 8rem 0;
  }
}
.subscription__title {
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
  font-size: 3.6rem;
}
.subscription__title::after {
  content: "";
  width: calc(100% + 1rem);
  height: 1rem;
  display: block;
  background-color: #84d9bc;
  margin-top: -1.5rem;
}
@media (min-width: 768px) {
  .subscription__title {
    margin-top: 0;
  }
}
.subscription__description {
  color: #FFFFFF;
  font-size: 1.8rem;
  line-height: 1.8;
}
.subscription__plans {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .subscription__plans {
    max-width: 90rem;
    flex-direction: row;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.subscription__plan {
  background-color: #FFFFFF;
  border-bottom: 1px solid #000;
  padding: 16rem 4rem 4rem 4rem;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .subscription__plan {
    border: none;
    transition-property: transform;
    transition-duration: 0.3s;
  }
  .subscription__plan:hover {
    transform: scale(1.1);
  }
}
.subscription__plan--basic {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.subscription__plan--business {
  background-color: #84d9bc;
  order: -1;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .subscription__plan--business {
    border-radius: 2rem;
    margin-top: -5rem;
    margin-bottom: -5rem;
    order: unset;
    z-index: 10;
  }
}
.subscription__plan--pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.subscription__subtitle {
  text-align: center;
  text-transform: uppercase;
}
.subscription__price {
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
}
.subscription__list li {
  margin-bottom: 2rem;
  text-align: center;
}
.subscription__list li:last-of-type {
  margin-bottom: 0;
}
.subscription__button {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
  margin-top: 2rem;
}
.subscription__button:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .subscription__button {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .subscription__button {
    display: block;
  }
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer__grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__img {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
.footer__box {
  margin-bottom: 6rem;
  text-align: center;
}
.footer__box:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
    text-align: left;
  }
}
.footer__box h3 {
  color: #84d9bc;
  font-size: 2.3rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .footer__box h3 {
    margin-bottom: 5rem;
  }
}
.footer__box li {
  line-height: 2.5;
}
.footer__box a {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.footer__copyright {
  border-top: 0.2rem solid #84d9bc;
  color: #FFFFFF;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}